import React, { useState } from 'react'
import emailjs from 'emailjs-com'
import './Contact.scss'
import { Button } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'
import Modal from '@material-ui/core/Modal'

const useStyles = makeStyles((theme) => ({
	paper: {
		position: 'relative',
		width: 400,
		backgroundColor: theme.palette.background.paper,
		border: '2px solid whitesmoke',
		boxShadow: theme.shadows[5],
		padding: theme.spacing(2, 4, 3),
	},
}))
function Contact({ index, active }) {
	const [show, setShow] = useState(false)

	const handleClose = () => {
		setShow(false)
	}
	const handleShow = () => {
		console.log('hi')
		setShow(true)
		console.log(show)
	}
	const sendEmail = (e) => {
		e.preventDefault()

		emailjs
			.sendForm(
				'service_bbzc5rs',
				'template_grvnh1p',
				e.target,
				'user_wXRljOf2CWdpwCyEq6Zjl'
			)
			.then(
				(result) => {
					console.log(result.text)
				},
				(error) => {
					console.log(error.text)
				}
			)

		handleShow()
		e.target.reset()
	}
	const classes = useStyles()

	const body = (
		<div className={classes.paper}>
			<h2 style={{ marginBottom: '20px' }} id='simple-modal-title'>
				Message Sent
			</h2>
			<p id='simple-modal-description'>I will contact you soon</p>
			<Button
				style={{ position: 'absolute', right: '10px', bottom: '10px' }}
				variant='contained'
				color='primary'
				onClick={handleClose}>
				Close
			</Button>
		</div>
	)
	return (
		<div
			className={`contact ${
				active === index ? 'display' : 'notDisplay'
			}`}>
			<Modal
				open={show}
				onClose={handleClose}
				style={{
					display: 'flex',
					justifyContent: 'center',
					alignItems: 'center',
				}}
				aria-labelledby='simple-modal-title'
				aria-describedby='simple-modal-description'>
				{body}
			</Modal>
			<h1>Contact</h1>
			<form className='contact__form' action='' onSubmit={sendEmail}>
				<input type='text' name='name' placeholder='name' required />
				<input type='email' name='email' placeholder='email' required />
				<input
					type='text'
					name='subject'
					placeholder='subject'
					required
				/>
				<textarea
					name='message'
					id=''
					cols='30'
					rows='10'
					placeholder='Message....'
					required></textarea>
				<Button variant='contained' type='submit'>
					Send
				</Button>
			</form>
		</div>
	)
}

export default Contact
